<div id="control-panel"><div class="progress-bar-wrapper" @click="jumpToTime($event)"><div class="progress-bar-buffered" :style="`width: ${bufferTime / duration * 100}%`"></div><div class="progress-bar-played" :style="`width: ${currentTime / duration * 100}%`"></div></div><div class="progress-bar-time-wrapper"><span class="progress-bar-time">{{ durationFormat(currentTime) }}</span><span class="progress-bar-time">{{ durationFormat(duration) }}</span></div><div class="controller"><div class="table__cell__4 song-info-wrapper"><div class="table__cell__3 song-info-album-wrapper"><img :src="currentSong?.coverUrl" alt="/"></div><div class="table__cell__9 song-info"><div class="song-info-name">{{ currentSong?.name }}</div><div class="song-info-artist">{{ currentSong?.artist }}</div></div></div><div class="table__cell__4 controller-main"><span class="button button-shuffle" :class="{'button-shuffle-toggled': isShuffle}" @click="shufflePlayToggle"></span><span class="button button-presong" @click="prevSongPlay"></span><span class="button button-controller-wrapper" @click="togglePlay"><span :class="[ isPlaying ? 'button-pause' : 'button-play']"></span></span><span class="button button-nxtsong" @click="nextSongPlay"></span><span class="button button-repeat" :class="{'button-repeat-toggled': isLoop}" @click="loopPlayToggle"></span></div><div class="table__cell__4 controller-sub"><div class="table__cell__8 volume-controller"><img src="../assets/svg/controlPanel/ic_volume_up_24px.svg"><div class="volume-progress-bar-wrapper" @click="volumeUpdate($event)"><div class="volume-progress-bar" :style="`width: ${volume * 100}%`"></div></div></div><div class="table__cell__4 button" :class="[currentSong.like ? 'button-like' : 'button-not-like-yet']" @click="toggleSongLike"></div></div></div></div>