<div id="album"><div class="album-info-wrapper"><div class="table__cell__3 album-cover-wrapper"><img class="album-cover" :src="data.coverUrl"></div><div class="table__cell__9 album-info"><div class="album-publish-years">{{ data.years }}</div><div class="album-title">{{ data.name }}</div><div class="album-button-play-all-songs" @click="playSong(data.songList[0].id)">PLAY</div></div></div><div class="album-songlist"><div class="album-songlist-table-header"><div class="table__cell__2"></div><div class="table__cell__5 album-songlist-song-name">TITLE</div><div class="table__cell__2 album-songlist-song-length">LENGTH</div><div class="table__cell__3 album-songlist-song-like"><img src="../../assets/svg/album/heart-header.svg"></div></div><div class="album-songlist-table-row" v-for="(song, index) in data.songList" :key="song.id" @click="playSong(song.id)"><div class="table__cell__2 album-songlist-song-index"><img v-show="song.id === currentSong?.id" src="../../assets/svg/album/ic_music_note_24px.svg"><span>{{ index + 1 }}</span></div><div class="table__cell__5 album-songlist-song-name">{{ song.name }}</div><div class="table__cell__2 album-songlist-song-length">{{ durationFormat(song.duration) }}</div><div class="table__cell__3 album-songlist-song-like"><span>{{ likeNumberFormat(Math.floor(Math.random() * 5000 ) + 10000) }}</span><div class="button-song" :class="[song.like ? 'button-song-like' : 'button-song-notlike-yet']" @click="toggleSongLike($event, song.id)"></div></div></div></div></div>